@use 'sass:color';

.miewer {
  pointer-events: none;
  position: absolute;
  inset: 0;
  overflow: auto;

  .mw-miewer-header {
    margin-bottom: 2px;
    border-bottom: 1px solid transparent;
    padding: 2px 10px;
    pointer-events: auto;
    z-index: 3;

    .mw-miewer-header-toggle {
      display: inline-flex;
      align-items: center;
      line-height: 1;
    }
  }
}

.mw-header-menu-item-icon {
  font-size: larger;
}

.mw-header-menu-item-text {
  display: flex;
  width: 100%;
  min-width: 100px;
  align-items: center;

  > *:not(:last-child) {
    margin-right: 5px;
  }
}

@mixin miewer($bg, $fg, $header, $header-border) {
  .miewer {
    background: $bg;
    color: $fg;

    .mw-miewer-header {
      background: $header;
      border-bottom: 1px solid $header-border;
    }
  }
}
